@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
:root {
  /* Fonts */
  --text: "Nunito", sans-serif;
  --title: "Itim", cursive;

  /* Colors */
  --light: #ffffff;
  --gray: #dfdfdf;
  --dark: #292f36;
  --primary: #ff4747;
  --accept: #08c485;
  --alert: #d0253c;
  --blue: #1e96fc;

  /* Spacing */
  --margin: 0.6rem;
  --padding: 0.4rem;

  /* Border Radius */
  --radius: 0.2rem;

  /* Box Shadow */
  --shadow: 1px 1px 6px 1px #292f36;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.button {
  border: none;
  padding: var(--padding);
  background-color: var(--primary);
  color: var(--light);
  border-radius: var(--radius);
  font: 1.2rem var(--title);
  cursor: pointer;
  transition: all ease 0.2s;
  box-shadow: inset 0px 0px 0px 50px rgba(255, 71, 71, 0.5);
}

.button:hover {
  background-color: var(--light);
  color: var(--primary);
  box-shadow: inset 0px 0px 0px 2px var(--primary);
}

.header {
  display: flex;
  justify-content: space-between;
  padding: var(--padding);
  background-color: var(--light);
  border-bottom: 2px dashed var(--gray);
}

.header__logo {
  color: var(--primary);
  font: 2rem var(--title);
}

.input {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: var(--padding);
  margin: var(--margin) 0;
}

.input__label {
  font: 1.2rem var(--text);
}

.input__field {
  border: none;
  border-bottom: 2px solid var(--primary);
  font: 1.2rem var(--text);
  padding: 0 var(--padding);
}

.input__field:focus {
  outline: none;
}

.form__label {
  font: 1.2rem var(--text);
  margin: var(--margin);
}

.checkbox {
  height: 30px;
  width: 30px;
  background-color: var(--gray);
  border-radius: var(--radius);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.checkbox--checked {
  height: 15px;
  width: 30px;
  border-left: 6px solid var(--accept);
  border-bottom: 6px solid var(--accept);
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: var(--padding);
  -webkit-animation: fadeIn ease 0.8s;
          animation: fadeIn ease 0.8s;
  border: 1px solid var(--gray);
  border-radius: 0.2rem;
  padding: 0.4rem;
  transition: ease 0.3s;
}

.form:hover {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.statistics {
  height: 21rem;
  width: 40rem;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  border: 1px solid var(--gray);
  border-radius: 0.2rem;
  padding: 0.5rem;
  -webkit-animation: fadeIn ease 0.8s;
          animation: fadeIn ease 0.8s;
  transition: ease 0.3s;
}

.statistics:hover {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}

.statistics__container {
  height: 20rem;
  width: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.statistics__title {
  font: 1.2rem var(--text);
}

.statistics__info {
  font: 1rem var(--text);
}

.pipe {
  width: 1rem;
  background-color: var(--accept);
}

#pipe--blue {
  background-color: var(--blue);
}

#pipe--red {
  background-color: var(--primary);
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media (min-width: 580px) and (max-width: 767px) {
  .statistics {
    width: 35rem;
    padding: 0;
  }

  .statistics__title {
    display: none;
  }

  .statistics__container {
    width: 11rem;
  }
}

@media (min-width: 480px) and (max-width: 579px) {
  .statistics {
    width: 28rem;
    padding: 0;
  }

  .statistics__title {
    display: none;
  }

  .statistics__container {
    width: 9.3rem;
  }
}

@media (max-width: 479px) {
  .statistics {
    width: 17rem;
    padding: 0;
    height: 16rem;
  }

  .statistics__title {
    font: 1rem var(--text);
    margin: 0 0.2rem;
  }

  .statistics__info {
    display: none;
  }

  .statistics__container {
    width: 9.3rem;
    height: 14rem;
  }
}

.main {
  width: 100%;
  height: 25rem;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border-bottom: 2px solid var(--gray);
}

@media (max-width: 905px) {
  .main {
    flex-wrap: wrap;
    height: 47rem;
  }
}

.note {
  display: flex;
  justify-content: space-between;
  padding: var(--padding);
  border-bottom: 2px dashed var(--gray);
  border-left: 2px solid var(--gray);
  border-right: 2px solid var(--gray);
  width: 80%;
}

.note__container {
  width: 95%;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  overflow-x: hidden;
}

.note__advice {
  position: absolute;
  top: 0;
  right: 0;
  font: 1rem var(--text);
  color: var(--alert);
}

.note__text {
  font: 1.4rem var(--text);
  color: var(--dark);
}

@media (max-width: 1279px) {
  .note__advice {
    display: none;
  }
}

.noteList {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

